.page-container {
  /* border: 1px solid red; */
  margin: auto;
  max-width: 100rem;
}

.page {
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

.page-enter {
  opacity: 0;
  transform: scale(1.1);
}

.page-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms;
}

.page-exit {
  opacity: 1;
  transform: scale(1);
}

.page-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
/* end on route change transition */

/* to remove borders on full res nav bar */
.ui.secondary.pointing.menu {
  border: none;
}

input {
  font-size: 16px !important;
}

blockquote {
  font-size: 1.5rem;
  color: white;
}
blockquote p {
  text-align: left;
}

p {
  text-align: justify;
}
.ui.fluid.vertical.menu {
  position: sticky !important;
  top: 4rem;
}
